import React, { useContext } from 'react';
import Styles from '../Scss/Avatar.module.scss';
import Avatar from 'react-avatar-edit';
import Human from '../img/person_24dp_E8EAED_FILL0_wght400_GRAD0_opsz24.svg';
import { SearchContext } from '../App';

const AvatarUpLoader = () => {
  const [active, setActive] = React.useState<boolean>(false);
  const [preview, setPreview] = React.useState<null | string>(null);
  const { setImgSave } = useContext(SearchContext);
  const onClose = () => {
    setPreview(null);
  };

  const onCrop = (view: string) => {
    setPreview(view);
  };
  const ActiveBtn = () => {
    setActive(!active);
  };
  React.useEffect(() => {
    if (preview) {
      const SavedImg = localStorage.getItem('avatar');
      setPreview(SavedImg);
      setImgSave(SavedImg);
    }

    if (active) {
      document.body.style.overflow = 'hidden';
      return () => {
        document.body.style.overflow = 'auto';
      };
    } else {
      return () => {
        document.body.style.overflow = 'visible';
      };
    }
  }, [active]);
  const ClosePop = () => {
    setActive(false);
  };

  const SaveItem = () => {
    if (preview) {
      localStorage.setItem('avatar', preview);
      setImgSave(preview);
      setPreview(preview);
      setActive(false);
    }
  };

  return (
    <div className={Styles.container}>
      <div className={Styles.content}>
        <div className={Styles.blockImg}>
          {preview ? (
            <img src={preview} alt="Preview" />
          ) : (
            <div className={Styles.NonPic}>
              <img src={Human} alt="" />
            </div>
          )}

          <button onClick={ActiveBtn}> Choose Avatar</button>
        </div>
      </div>
      {active && (
        <div className={Styles.BlockAbs}>
          <div className={Styles.BlockAbs__Content}>
            <Avatar width={300} height={300} onCrop={onCrop} label="Choose a file" />
            <div className={Styles.buttonContainer}>
              <button onClick={SaveItem}>Save</button>
              <div className={Styles.sectionBtn}>
                <button onClick={ClosePop}>Close</button>
                <button onClick={onClose}>Delete</button>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default AvatarUpLoader;
